ul {
    margin-top: 0;
    list-style-type: none;
    padding: 0;
    // margin: 0 auto;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    .contact {
        border-radius: 0.25rem;
        min-width: 220px;
        background-color: $clr2;
        margin: 0.2rem;
        a {
            color: whitesmoke;
            text-decoration: none;
        }
    }
}

