        .main-header {
            padding: 0 10vw;
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            align-items: center;
            background-color: $clr1;
            .main-logo {
                margin: 0;
                color: $clr2;
                font-family: Roboto-Bold;
            }
            .navbar {
                color: $clr2;
                .nav-link {
                    padding: 0.5rem;
                    border-right: 0.01rem solid;
                    &:last-child {
                        border-right: none
                    }
                }
                .nav-link:hover {
                    border-right: 0.01rem solid $clr3;
                    color: $clr3;
                    &:last-child {
                        border-right: none
                    }
                }
            }
        }